.sidebar{
    flex:.8;
    position: sticky;
    top: 0;
    overflow-y: scroll;
    height: calc(100vh - 8rem);
    margin-right: 1rem;
    
}

.sidebar .sidebarGroups .sidebarGroup {
    padding:12px 0 1.2rem 1rem;
    display: flex;
    align-items: center;
  
}
.sidebar .sidebarGroups .sidebarGroup:first-child{
    margin-bottom: 3rem;
}
.sidebar .sidebarGroups .hr{
    margin-top: 3rem;
}
 
.sidebar .sidebarGroups .sidebarGroup:hover {
    background-color: #ffffff;
}
.sidebar .sidebarGroups .sidebarGroup .sidebarLink {
   display: flex;
   align-items: center;
}

.sidebar .sidebarGroups .sidebarGroup .currentUserImg{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    object-fit: cover;

}
.sidebar .sidebarGroups .sidebarGroup svg{
    font-size: 2rem;
    color: #221a1a;
    margin-right: 1rem;
}

.sidebar .sidebarGroups .sidebarGroup button{
  
    background-color: transparent;
    padding: .5rem 1.0rem;
    border:none;
    transition: all 0.5;
    text-transform: uppercase;
}



@media(max-width:1466px){
    .sidebar{
        width:70rem;
        
    }
}
@media(max-width:1156px){
    .sidebar{
        width:55rem;
        
    }
}
@media(max-width:918px){
    .sidebar{
        width:55rem;
        
    }
}
@media(max-width:361px){
    .sidebar{

      padding: 2rem 0;
      margin: 2rem 0;
        
    }
    .sidebarGroup h4{
        display:none;
    }
    .sidebarLink{
        display:flex;
        flex-direction: column;
     }
}
@media(max-width:655px){
    .sidebar{

      padding: 2rem 0;
      margin: 2rem 0;
        
    }
    .sidebarGroup h4{
        display:none;
    }
    .sidebarLink{
        display:flex;
        flex-direction: column;
     }
}