.story{
   
    background-color: #eeeeee;
    width: 90rem;
    margin:.5rem 1.5rem;
    padding: 3rem 1.5rem;
    border-radius:2rem;
}
.react-tabs__tab-list{
    display: flex;
    justify-content: space-between;
}

.react-tabs__tab{
    font-weight: bold !important;}

.react-tabs__tab--selected{
    background: none !important;
    border: none !important;
    background-color: none !important;
  border-bottom: .1rem solid #ffffff !important;
  color: #ffffff !important;
}
.storyGroups{
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    overflow-x:scroll;
   
}

.storyGroups .storyGroup{
    margin: 1.5rem 0;
    flex: 0 0 auto;
   border-radius: 2.0rem;
    width: 15rem;
    height: 25rem;
    margin-right: 3.0rem;
    background-color: #ffffff;
   
}
::-webkit-scrollbar{
    height: .5rem;
}
::-webkit-scrollbar-track{
 background:  #ffffff ;
}
::-webkit-scrollbar-thumb{
 background-color:#888;
}

.storyGroups::-webkit-scrollbar-thumb:hover{
    background-color:#555 ;
}

.storyGroups .storyGroup .storyCard{
    height: 100%;
}

.storyGroups .storyGroup .storyCard .hidden{
    display:none;
}


.storyGroups .storyGroup .storyCard label{
display: flex;
flex-direction: column;
height: 100%;
cursor: pointer;
}

.storyGroups .storyGroup .storyCard label .storyBody{
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}
.storyGroups .storyGroup .storyCard label .storyBody img{
    width:100%;
    height:100%;
    object-fit:cover;
    border-radius: 2rem 2rem 0 0 ;
}
.storyGroups .storyGroup .storyCard label .storyFooter{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  position: relative;

}
.storyGroups .storyGroup .storyCard label .storyFooter svg{
position: absolute;
top: -2rem;
font-size:3rem;
padding: .5rem;
background-color: #ffffff;
border-radius: 50%;
}

.storyGroups .storyGroup .storyCard label .storyFooter h4{
    margin-top:2rem;

}

.storyGroups .storyGroup .storyDiv{
  position:relative;
} 
.storyGroups .storyGroup .storyDiv .storyUser img{
    height:25rem;
    width: 100%;
    object-fit:cover;
    border-radius: 2rem;
}


.storyGroups .storyGroup .storyDiv .storyUserTitle{
    position: absolute;
    bottom: 1.5rem;
    left: .5rem;
   color: rgb(8, 243, 8);
   font-weight: bold;
   
   font-size: 2.4rem;
}

.storyGroups .storyGroup .storyDiv .storyUserAddImgDiv{
      position: absolute;
      top: 2rem;
      left: .5rem; 
}

.storyGroups .storyGroup .storyDiv .storyUserAddImg{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    object-fit: cover;
}
.addStory{
    display: block;
    margin:0 auto;
    border: none;
    outline: none;
    cursor: pointer;
    padding: .2rem .5rem;
    border-radius: .5rem;
    color: #000000;
    font-weight: bold;
    transition: all 0.5s;
    font-size: 1.4rem;
}

.addStory:hover{
    color:red;
}




@media(max-width:1466px){
    .story{
        width:70rem;
        
    }
}
@media(max-width:1156px){
    .story{
        width:55rem;
        
    }
}
@media(max-width:918px){
    .story{
        width:55rem;
        
    }}
@media(max-width:655px){
    .story{
        width:60rem;
        
    }
}
@media(max-width:400px){
    .story{
    width: 60rem;
        
    }
}