


.unFriend{
    display: block;
    margin: 0 200px;
    padding: 5px 20px;
    text-transform: uppercase;
    font-weight:bold;
    border: none;
    background-color:rgb(8, 247, 84);
    color:white;
    border-radius: 10px;
    font-size: 10px;
  transition: all 0.5s;
    cursor: pointer;
    box-shadow: 0 0 3px #000000;
}
.unFriend:hover{
    background-color:rgb(250, 6, 6);
    color:rgb(255, 255, 255);
}
