
.accountDiv{
    width: 1000px;
    display: block;
    margin: 0 auto;
    position: relative;
  
 
}


.accountCover{
    position:absolute;
    top:-65px;
    border-radius: 20px;
     width: 100%;
     height:400px;
     cursor: pointer;
}
.accountCover img{
 width: 100%;
 height: 100%;
 object-fit: cover;
 border-radius: 20px;
 cursor: pointer;
}
.accountInfo{
    position: relative;
}
.accountInfoLeft{
    position: absolute;
    top: 280px;
    left: 40px;
}
.accountInfoLeft img{

 width:180px;
 height:180px;
 object-fit: cover;
 border-radius: 50%;
 cursor: pointer;

}

.accountInfoRight{
    position: absolute;
    top: 340px;
    left: 240px;
}
.userName{
    margin-bottom: 10px;
    font-weight: bold;
}
.friends{
   display: flex;
   align-items: flex-end;
   margin-top: 10px;
}
.friends img{
    width:50px;
    height:50px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 5px;
    cursor: pointer;
}
.dots{
    font-size: 30px;
}

.mainContainer.d-flex{
    display:grid;
}
.nADiv{
    display:flex;
    margin-top: 550px;
}

.nAPost{
   
    flex: 4;
    
    margin-right: 20px;
    border-radius: 20px;
}

.nAShareDiv{
    display: flex;
    flex-direction: column;
    align-items: center;
   
    border-radius: 20px;
   height: calc(100vh - 80px);
   overflow-y: scroll;
    

}

.nAShareDiv::-webkit-scrollbar{
    display: none;
}


.addCoverImg{
   background-color: #000000;
   padding: 5px 15px;
   color: #ffffff;
   transition: all 0.5s;
   position: absolute;
   bottom: 10px;
   right: 30px;
   border-radius: 20px;
   text-transform: uppercase;
   box-shadow: 0 0 3px #000000;
}

.addCoverImg:hover{
   color: #000000;
   background-color: #ffffff;
   font-weight: bold;
}
.addProfileImg{
   background-color: #000000;
   padding: 5px 15px;
   color: #ffffff;
   transition: all 0.5s;
   position: absolute;
   bottom: -5px;
   right: 55px;
   border-radius: 20px;
   text-transform: uppercase;
   box-shadow: 0 0 3px #000000;
}

.addProfileImg:hover{
   color: #000000;
   background-color: #ffffff;
   font-weight: bold;
}

