.share{
   
    background-color: #eeeeee;
   
    margin: 4rem 1.5rem;
    padding: 3rem 1.5rem;
    border-radius:2rem;
    width: 90rem;
}

.shareGroups .shareGroup{
    padding: 1rem 0;
}
.shareGroups .shareGroup:first-child{
    display: flex;
    align-items: center;
    border-bottom: .3rem solid #ffffff;
}



.shareGroups .shareGroup .shareGroupLeft img{
    width:4rem;
    height:4rem;
    object-fit: cover;
    border-radius:50%;
}

.shareGroups .shareGroup .shareGroupRight input{
    border: none;
    outline: none;
    width: 62rem;
    height: 4rem;
    margin-left: 1rem;
    padding-left: 1rem;
    border-radius: 1rem;
}
.shareGroup:last-child{
    display: flex;
}

.shareGroupIcons{
     flex: 3;
     display: flex;
     margin-top: 2rem;
    }
    .shareGroupIcon{
      margin-right: 2.5rem;
      cursor: pointer;
    }
    .shareGroupIcon:hover{
        border-bottom: .1rem solid #000000;
    }
    .shareGroupIcon svg{
        margin-right: .5rem;
    }

    .shareGroupIcon:first-child svg{
        color: #00ff00;
    }
    .shareGroupIcon:nth-child(2) svg{
        color:blue;
    }
    .shareGroupIcon:nth-child(3) svg{
        color: #ff0000;
    }
    .shareGroupIcon:last-child svg{
        color:goldenrod;
    }


   
.shareGroupShareBtn{
    flex: 1;
    margin-top: 2.0rem;
}
.shareGroupShareBtn button{
    display: block;
    margin: 0 0 0 auto;
    background-color: #ffffff;
    border: none;
    padding: .5rem 1.5rem;
    text-transform: uppercase;
    transition: all 0.5s;
    font-weight: bold;
    border-radius: .5rem;
}
.shareGroupShareBtn button:hover{
    background-color: #000000;
    color: #ffffff;
}



@media(max-width:1466px){
    .share{
        width:70rem;
        
    }
}
@media(max-width:1156px){
    .share{
        width:55rem;
        
    }
}
@media(max-width:918px){
    .share{
        width:55rem;
        
    }
}
@media(max-width:361px){
    .share{
      width:60rem;
   
        
    }
}