.nsap{
   
    background-color: #eeeeee;
    width: 90rem;
    margin: .5rem 1.5rem;
    padding: 3.0 1.5rem;
    border-radius:2.0rem;
}


.nsapUserImg{
    width: 4.0rem;
    height: 4.0rem;
    object-fit: cover;
    border-radius:50%;
    margin-right: .5rem;
}

.nsapCardHeader{
    display: flex;
    justify-content:space-between;
    align-items: center;
  
}

.nsapCardHeaderLeft{
    display: flex;
    align-items: center; 
}
.nsapCardHeaderLeft .nsapInfo{
    display: flex;
    flex-direction: column;
}

.nsapUserName{
    
    font-weight: bold;
}
.nsapDate{
    font-size: 1.2rem; 
}

.postCardHeaderRight svg{
    cursor: pointer;
    font-weight:bold;
}

.nsapCardBody{
    padding: 1.5rem 0;
}

.nsapText{
    margin-bottom: .5rem;
}

.nsapImg{
    width: 100%;
    max-height: 50rem;
    object-fit:contain;
}

.nsapCardFooter{
 display: flex;
 justify-content: space-between;
 align-items: center;
}
.nsapCardFooterLeft{
    display: flex;
    align-items: center;
    
}
.nsapCardFooterLeft img{
    width: 2.5rem;
    height:auto;
    cursor: pointer;
    margin-right: 1.0rem;
}
.nsapCounter,.postComments{
    font-size: 1.4rem;
    font-weight: bold;
}