.news {
  flex: 4;
  background-color: #ffffff;
  height: calc(100vh - 8rem);
  overflow-y: scroll;
}

.news::-webkit-scrollbar {
  display: none;
}

@media (max-width: 361px) {
  html {
    font-size: 30%;

    position: fixed;
  }

  .news {
    flex: 10;
  }
}
@media (max-width: 655px) {
  html {
    font-size: 40%;

    /* position: fixed; */
  }

  .news {
    flex: 10;
  }
}
