.rightbar {
  flex: 0.7;
  margin-top: 1rem;
  height: calc(100vh - 8rem);
}

.online {
  margin-bottom: 3rem;
}
.onlineGroup {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 2px;
  cursor: pointer;
  transition: all 0.5s;
}
.onlineGroup:hover {
  background-color: #eeeeee;
}
.onlineUserImage {
  width: 4rem;
  height: 4rem;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 1rem;
}
.onlineGroupLeft {
  display: flex;
  position: relative;
}
.OnlineBadge {
  position: absolute;
  bottom: 0px;
  right: 1rem;

  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #00ff00;
}

@media (max-width: 1156px) {
  html {
    font-size: 50%;
  }




}
@media (max-width: 361px) {
  html {
    font-size: 30%;
  }

  .rightbar {
    position: absolute;
    top: 50%;
  }
  .onlineGroup{
    display: flex;
    flex-direction: column;
  }
  .onlineGroupLeft{
    margin: 1rem 0;
  }
}
@media (max-width:655px) {
  html {
    font-size: 30%;
  }

  .rightbar {
    position: absolute;
    top: 50%;
  }
  .onlineGroup{
    display: flex;
    flex-direction: column;
  }
  .onlineGroupLeft{
    margin: 1rem 0;
  }
}
