
.post{
   
    background-color: #eeeeee;
    width: 90rem;
    margin: .5rem 1.5rem;
    padding: 3rem 1.5rem;
    border-radius:2rem;
}
.postUserImg{
    width: 4rem;
    height: 4rem;
    object-fit: cover;
    border-radius:50%;
    margin-right: .5rem;
}

.postCardHeader{
    display: flex;
    justify-content:space-between;
    align-items: center;
  
}

.postCardHeaderLeft{
    display: flex;
    align-items: center; 
}
.postCardHeaderLeft .postInfo{
    display: flex;
    flex-direction: column;
}

.postUserName{
    
    font-weight: bold;
}
.postDate{
    font-size: 1.2rem; 
}

.postCardHeaderRight svg{
    cursor: pointer;
    font-weight:bold;
}

.postCardBody{
    padding: 1.5rem 0;
}

.postText{
    margin-bottom: .5rem;
}

.postImg{
    width: 100%;
    max-height: 50rem;
    object-fit:contain;
}

.postCardFooter{
 display: flex;
 justify-content: space-between;
 align-items: center;
}
.postCardFooterLeft{
    display: flex;
    align-items: center;
    
}
.postCardFooterLeft img{
    width: 2.5rem;
    height:auto;
    cursor: pointer;
    margin-right: 1.0rem;
}
.postCounter,.postComments{
    font-size: 1.4rem;
    font-weight: bold;
}




@media(max-width:1466px){
    .post{
        width:70rem;
        
    }
}
@media(max-width:1156px){
    .post{
        width:55rem;
        
    }
}
@media(max-width:918px){
    .post{
        width:55rem;
        
    }
}
@media(max-width:361px){
    .post{
        width:60rem;
        
    }
}