.friend{
    display: block;
    margin: 0 auto;
    width: 900px;
}
.friendTop{
    display: flex;
    align-items: center;
    margin-top: 100px;
}

.friendTop .topSearch{
    position: relative;
    margin-left:10px;
}   

.friendTop .topSearch input{
    border: none;
    outline: none;
    width: 200px;
    border-radius: 20px;
    background-color: rgb(238,238,238);
    height: 30px;
    color: #000000;
    padding: 0px 10px;
}

.friendTop .topSearch svg{
    position: absolute;
    right: 10px;
    color: #a5a5a5;
    top: 50%;
    transform: translateY(-50%);

}


.friendGroups{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
    overflow-y: scroll;
}

.friendGroup{
    display: flex;
    align-items: center;
    width: 400px;
    margin-right: 5px;
    margin-bottom: 50px;
    margin-top: 30px;
    
}

.friendGroups img{
    width:80px;
    height:80px;
    border-radius: 50%;
    margin-right: 5px;
    
}

.friendName{
    font-weight: bold;
    margin-right: 60px;
}


.friendGroups button{
    display: block;
    margin: 0 auto;
    padding: 5px 20px;
    text-transform: uppercase;
    font-weight:bold;
    border: none;
    background-color:rgb(8, 247, 84);
    color:white;
    border-radius: 10px;
    font-size: 10px;
}
.friendGroups button:hover{
    background-color:white;
    color:rgb(8, 247, 84);
}

.nofriend{
    margin: 200px 0;
}