.form{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.formGroups{
    width: 900px;
    height: auto;
    display: flex;
    border-radius: 20px;
    background-color: #eeeeee;
    padding: 20px 15px;
}
.form-left{
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
   
}
.form-Right{
    flex: 1;
}
.formSubTitle{
    font-size: 20px;
    letter-spacing: 1.3px;
    margin-bottom: 10px;

}
.formTitle{
    text-transform: uppercase;
    letter-spacing: 1.3px;
    font-size:40px;
}
.formGroup{
    margin-bottom: 20px;
}

.formGroup input{
    height: 40px;
    border:none;
    outline: none;
    padding: 0 10px;
    width: 100%;
    border-radius: 10px;

}
.form-btnLogin button{
    display: block;
    margin: 0 auto;
    padding: 5px 20px;
    text-transform: uppercase;
    font-weight:bold;
    border: none;
    background-color:blue;
    color:white;
    border-radius: 10px;
    font-size: 10px;
}
.form-btnLogin button:hover{
    background-color:white;
    color:blue;
}
.form-btnRegistration{
    margin-top: 60px;
}
.form-btnRegistration  button{
    display: block;
    margin: 0 auto;
    padding: 5px 20px;
    text-transform: uppercase;
    font-weight:bold;
    border: none;
    background-color:rgb(8, 247, 84);
    color:white;
    border-radius: 10px;
    font-size: 10px;
    cursor: pointer;
}
.form-btnRegistration button:hover{
    background-color:white;
    color:rgb(8, 247, 84);
}

