.top{
    position: sticky;
    top: 0;
    left: 0;
    background: #ffffff;
    z-index: 999;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8rem;
}

.topLeft,
.topMiddle,
.topRight{
    display: flex;
   align-items: center;
  
}




.topLeft .logo{
  font-size: 3.0rem;
  font-weight: bold;
  text-transform: uppercase;
}

.topLeft .topSearch{
    position: relative;
    margin-left:1.0rem;
}   

.topLeft .topSearch input{
    border: none;
    outline: none;
    width: 20rem;
    border-radius: 2.0rem;
    background-color: rgb(238,238,238);
    height: 3.0rem;
    color: #000000;
    padding: 0rem 1.0rem;
}

.topLeft .topSearch svg{
    position: absolute;
    right: 1.0rem;
    color: #a5a5a5;
    top: 50%;
    transform: translateY(-50%);

}

.topMiddle .topLink ul{
    display: flex;
      
}
.topMiddle .topLink ul li{
    margin: 0 1.0rem;
      
}
.topMiddle .topLink ul li a{
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.5s;
      
}
.topMiddle .topLink ul li a:hover{
    border-bottom: .3rem solid #000000;  
      
}



 
.topMiddle .topLink ul li .active{
   border-bottom: .2rem solid #000000;
  
      
}

.topRight .notifications{
 display: flex;
}

.topRight .notifications .notification{
   margin: 0 1.5rem;
   cursor: pointer;
   position: relative;
   font-size: 2.0rem;
 }
.topRight .notifications .notification .badge{
   position: absolute;
   top: -1.0rem;
   left: 1.0rem;
   background-color: #ff0000;
   width: 1.5rem;
   height:1.5rem;
   border-radius: 50%;
   padding: 1.0rem;
   display: flex;
   justify-content: center;
   align-items: center;
   color:#ffffff;
   font-weight: bold;
  

 }
 .topRight .user {
    margin-left: 4rem;
    display: flex;
    align-items: center;
 }

 .topRight .user img{
     width: 5rem;
     height: 5rem;
     border-radius: 50%;
     object-fit: cover;
    
 }

 .topRight .user .userLogout{
    cursor: pointer;
 }
 .topRight .user .userLogout:hover{
   color: #a5a5a5;
 }


 



@media(max-width:768px){
    html{
        font-size: 50%;
    }
}
@media(max-width:361px){
    html{
        font-size: 30%;
    }

    .topLeft, .topMiddle,
    .topRight{
        display: flex;
        flex-direction: column;
    }
    .topLeft .topSearch{
        position:absolute;
        margin: 5rem 0;
       font-size: 2rem;
    } 
    .topMiddle .topLink {
        position: absolute;
        left: 30rem;
    }
    .topMiddle .topLink ul{
        position: absolute;
        bottom: -5rem;
        font-size: 2rem;
    }
    .topRight .notifications{
        position: absolute;
        left: 35rem;
        top: 2.5rem;
        
    }
    .user{
        display: flex;
        flex-direction: column;
        position: absolute;
        left:58rem;
        top:1rem;
       
    }
    .userImage{
        margin-bottom: 1rem ;
    }
    .userLogout{
        font-size: 2rem;
    }
}
